.recent-head{
    font-weight: bold;
    font-size: 18px;
    margin-top: 10px;
    margin-left: 10px;
}

.recent-sec-ul{
    list-style: none;
    color: #000;
    margin-left: -40px;
}

.recent-sec{
    overflow: auto;
    white-space: nowrap;
    overflow-x: scroll;
    color: #000;
    overflow-y: hidden;
}

::-webkit-scrollbar {
    width: 0;  /* Remove scrollbar space */
    background: transparent;  /* Optional: just make scrollbar invisible */
}
  
.recent-sec-li {
    display: inline-flex;
    color: #000;
    text-align: center;
    padding: 14px;
    text-decoration: none;
    scroll-behavior: smooth;
}

.recent-li-sec{
    height: 200px;
    width: 190px;
    border-radius: 10px;
    margin-right: 20px;
    font-family: 'Source Sans Pro', sans-serif;

}

.recent-li-sec img{
    height: 200px;
    width: 190px;
    border-radius: 10px;
    object-fit: cover;
}

.recent-li-down{
    position: sticky;
    margin-top: -73px;
    width: 184px;
    height: 10vh;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: #000;
    color: white;
    text-align: left;
    padding-left: 6px;
    opacity: 0.8;
    overflow: hidden;
    white-space: normal;
    position: relative;
    z-index: 0.2;
}


.recent-down-title{
    padding-top: 15px;
}
 
.recent-down-details{
    width: 180px;
    height: 40px;
    font-size: 13px;
}

.recent-down-details-span{
    font-weight: bold;
}

@media (min-width: 400px) and (max-width: 800px) {
    .recent-li-down{
        margin-top: -67px;
    }
}

@media (min-width: 800px) and (max-width: 1500px) {
    .recent-li-down{
        margin-top: -67px;
    }
}

@media screen and (max-width: 400px) {
    .recent-li-down{
        margin-top: -79px;
    }
}

@media screen and (max-width: 500px) {
    .recent-li-down{
        margin-top: -81px;
    }
}