form{
    font-family: 'Poppins', sans-serif;
}

.signup-fill{
    padding-left: 30vw;
    margin-top: 40px;
    margin-bottom: 4vw;
}

input::placeholder{
    font-family: 'Poppins', sans-serif;
    font-size: 17px;
}


.head-center{
    display: flex;
    margin-left: 10vw;
    margin-bottom: 36px;
    font-size: 19px;
}

.head-center p{
    text-align: center;
    align-items: center;
    margin-top: 5px;
}

.h2{
    color: black;
    margin-right: 5px;
}

.d{
    opacity: 0.7;
}

input{
    border-radius: 5px;
    background-color: #ccc;
    border: none;
    outline: none;
    height: 50px;
    width: 500px;
    padding: 7px;
    display: block;
    margin-bottom: 15px;
    padding-left: 8px;
    font-family: 'Poppins', sans-serif;
}

.form{
    margin-right: 27px;
}


.f{
    display: flex;
}

.btn{
    width: 130px;
    height: 41px;
    border-radius: 30px;
    margin-left: 14vw;
    margin-top: 39px;
    margin-bottom: 29px;
}

@media screen and (max-width: 500px){
    .signup-fill{
        padding-left: 3vw;
    }

    input{
        width: 410px;
    }

    .group input{
        width: 187px;
    }

    .form{
        margin-right: 20px;
    }

    .head-center{
        margin-left: 23%;
        margin-bottom: 17px;
    }
}