.hero-img{
    object-fit: cover;
    width: 100%;
    height: 70vh;
}

.hero-text-p{
    width: 30vh;
}

.hero-text-button{
    border: none;
    height: 30px;
    margin-top: 10px;
    width: 150px;
    outline: none;
    color: #fff;
    background-color: rgb(78, 118, 248);
    border-radius: 8px;
    font-family: 'Poppins', sans-serif;
    cursor: pointer
}

.img{
    height: 70vh;
}

.hero-text{
    position: absolute;
    top: 70vh;
    height: 17vh;
    width: 100%;
    color: #fff;
    align-items: center;
    text-align: center;
}

@media (min-width: 400px) and (max-width: 800px) {
    .hero-text-p{
        margin-left: 18vh;
        width: 80vh;
        font-size: 26px;
        margin-top: -5vh;
        margin-bottom: 2vh;
    }

    .hero-text-button{
        margin-left: 20%;
    }
}

@media (min-width: 400px) and (max-width: 600px) {
    .hero-text-p{
        margin-left: 58vh;
        width: 80vh;
        font-size: 26px;
        margin-top: -7vh;
        margin-bottom: 3vh;
    }
}

@media (min-width: 800px) and (max-width: 1500px) {
    .hero-text-p{
        margin-left: 57vh;
        width: 80vh;
        font-size: 26px;
        margin-top: -5vh;
        margin-bottom: 2vh;
    }
}