*{
    margin: 0;
    top: 0;
    left: 0;
}

.navbar{
    padding: 10px;
    background-color: rgb(31, 31, 31);
    color: #fff;
}

.logo{
    margin-top: 3px;
    text-align: center;
    align-items: center;
    font-family: 'Poppins', sans-serif;
    font-size: 18px;
}



.hamburger{
    position: absolute;
    color: beige;
    top: 18px;
    left: 20px;
    height: 23px;
    width: 26px;
    z-index: 1;
    cursor: pointer;
}

.hamburger.active :nth-child(2){
    opacity: 0;
}

.hamburger.active:nth-child(1){
    transform: rotate(45deg);
    transition: .5s;
}

.hamburger.active:nth-child(3){
    transform:  rotate(-45deg);
    transition: .5s;
}

.hi{
    background-color: #fff;
    height: 3px;
    margin-bottom: 3px;
    border-radius: 3px;
    cursor: pointer;
}

.top{
    border-radius: 3px;
}

.under{
    display: flex;
    padding: 12px;
    padding-left: 20px;
    justify-content: space-between;
    text-align: center;
    align-items: center;
    background-color: #fff;
    opacity: 90%;
    position: sticky;
}

.signup{
    border: none;
    height: 30px;
    width: 76px;
    outline: none;
    color: #fff;
    background-color: rgb(78, 118, 248);
    border-radius: 8px;
    font-family: 'Poppins', sans-serif;
    cursor: pointer;
}

.prompt{
    font-weight: 400;
    font-family: 'Inter', sans-serif;
    font-size: 14px;
}

.list{
    margin-top: 3%;
    position: absolute;
    z-index: 1;
    list-style: none;
    width: 100%;
}

.a{
    margin-left: 15px;
    margin-top: -10px;
    display: inline-block;
}

.li{
    margin-bottom: 40px;
    font-size: 15px;
    margin-top: 10;
    cursor: pointer;
}


.lef:after {
    color: #777;
    font-weight: bold;
    margin-left: 75%;
}

.after {
    content: "\2212";
}

.panel {
    overflow: hidden;
    display: block;
    transition: max-height 0.2s ease-out;
}

.vi{
    width: 100%;
    position: absolute;
    height: 52vh;
    background-color: #000;
    color: #fff;
    opacity: 0.9;
    z-index: 1;
    transition: 1s ease-in-out;
    overflow: hidden;
    animation: cool 1.5s;
}

.link{
    color: #fff;
    text-decoration: none;
}

.log{
    font-family: 'Poppins', sans-serif;
}

.lef{
    color: #777;
    margin-left: 82%;
}


@keyframes cool {
   0%{
       height: 10vh;
   }
   100%{
       height: 52vh;
   }
}

@media screen and (max-width: 400px) {
    @keyframes cool {
        0%{
            height: 10vh;
        }
        100%{
            height: 57vh;
        }
    }
}

@media (min-width: 450px) and (max-width: 800px) {
    @keyframes cool {
        0%{
            height: 10vh;
        }
        100%{
            height: 47vh;
        }
    }
}

@media (min-width: 850px) and (max-width: 1500px){
    @keyframes cool {
        0%{
            height: 10vh;
        }
        100%{
            height: 57vh;
        }
    }
}

@keyframes free {
    0%{
        height: 10vh;
    }
    100%{
        height: 22vh;
    }
}
