form{
    font-family: 'Poppins', sans-serif;
}

.group{
    display: flex;
}

.group input{
    width: 230px;
}

.signup-fill{
    padding-left: 30vw;
    margin-top: 40px;
    margin-bottom: 4vw;
}

input::placeholder{
    font-family: 'Poppins', sans-serif;
    font-size: 17px;
    padding-left: 8px;
}


.head-center{
    display: flex;
    margin-left: 10vw;
    margin-bottom: 44px;
    font-size: 19px;
}

.head-center p{
    text-align: center;
    align-items: center;
    margin-top: 5px;
}

.h2{
    color: black;
    margin-right: 5px;
}

.d{
    opacity: 0.7;
}

input{
    border-radius: 5px;
    background-color: #ccc;
    border: none;
    outline: none;
    height: 50px;
    width: 500px;
    padding: 7px;
    display: block;
    margin-bottom: 15px;
}

.form{
    margin-right: 27px;
}

.tick{
    border-radius: 5px;
    background-color: #ccc;
    border: none;
    outline: none;
    height: 15px;
    width: 15px;
    padding: 7px;
    display: inline-flex;
    margin-top: 5px;
    margin-bottom: 0px;
    margin-right: 9px;
}

.f{
    display: flex;
}

.btn{
    width: 130px;
    height: 41px;
    border-radius: 30px;
    margin-left: 14vw;
    margin-top: 39px;
    margin-bottom: 29px;
}

.error{
    background-color: red;
    width: 500px;
    height: 70px;
    align-items: center;
    text-align: center;
    border-radius: 7px;
    font-size: 17px;
    color: #fff;
    opacity: 0.8;
    margin-top: -33px;
}

.error p{
    padding-top: 20px;
}

.active{
    border: solid 1px red;
}

.perror{
    margin-bottom: 10px;
    color: red;
}

@media screen and (max-width: 500px){
    .signup-fill{
        padding-left: 3vw;
    }

    input{
        width: 410px;
    }

    .group input{
        width: 187px;
    }

    .form{
        margin-right: 20px;
    }

    .head-center{
        margin-left: 23%;
        margin-bottom: 17px;
    }

    .btn{
        margin-left: 33%;
        margin-top: 20px;
    }
}

@media screen and (max-width: 400px) {
    .signup-fill{
        padding-left: 3vw;
    }

    input{
        width: 340px;
    }

    .group input{
        width: 154px;
    }

    .form{
        margin-right: 20px;
    }

    .head-center{
        margin-left: 20%;
        margin-bottom: 17px;
    }

    .btn{
        margin-left: 33%;
        margin-top: 20px;
    }
}