.blog{
    width: 70vw;
    min-width: 400px;
    height: 800px;
    display: block;
    margin: auto;
    padding: 50px 0;
}
  
textarea::-webkit-scrollbar{
    width: 10px;
}
  
textarea::-webkit-scrollbar-thumb{
    background: rgba(0, 0, 0, 0.1);
    border: 10px;
}
  
.title,
.article{
    width: 100%;
    min-height: 100px;
    height: auto;
    outline: none;
    font-size: 50px;
    font-weight: 600;
    color: #2d2d2d;
    resize: none;
    border: none;
    padding: 10px;
    border-radius: 10px;
}
  
.title::placeholder,
.article::placeholder{
    color: #2d2d2d;
}
  
.article{
    height: 500px;
    font-size: 20px;
    margin-top: 20px;
    line-height: 30px;
    font-weight: 500;
    padding-bottom: 100px;
    white-space: pre-wrap;
}
  
.blog-options{
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    background: #fff;
    z-index: 9;
    display: flex;
    justify-content: center;
    align-items: center;
}
  
.cbtn{
    border: none;
    outline: none;
    cursor: pointer;
}
  
.cbtn.grey{
    background: #a5a5a5;
    color: #fff;
    margin-left: 20px;
    font-size: 14px;
}

.modal{
    width: 330px;
    background-color: #ccc;
    border-radius: 10px;
    margin-left: 20px;
    padding: 10px;
    padding-left: 20px;
    position: absolute;
    top: 40%;
    left: 35%;
}

.modal select{
    width: 300px;
    height: 40px;
    border-radius: 7px;
    border: none;
    padding-left: 10px;
    outline: none;
    margin-bottom: 10px;
    background-color: white;
}

.modal button{
    margin-top: 10px;
    margin-left: 35%;
    width: 90px;
}

.modal h2{
    margin-bottom: 5px;
}

.cbtn{
    height: 40px;
    border-radius: 7px;
    font-family: 'Poppins', sans-serif;
}


@media screen and (max-width: 500px) {
    .title{
        font-size: 40px;
        font-weight: 600;
        min-height: 40px;
    }

    .cpublish-btn{
        position: absolute;
        margin-top: 25%;
        margin-left: 85%;
        width: 90px;
    }

    .modal{
        left: 6%;
    }

    .cbtn{
        width: 50px;
        height: 50px;
        border-radius: 50%;
    }

    .big{
        height: 30px;
        width: 30px;
        margin-left: -5px;
        margin-top: 5px;
    }
}
